let _this = this
import config from '@/api/config.js'
import {request} from '@/api/reques.js'
import {goWindow} from '@/api/navigate.js' // 页面跳转
export default{
	data(){
		return{
			goodsList: [],
			page_count: 0,
		}
	},
	props:{
		num:{
			type:String
		},
		carId:{
			type:String
		},
		brandId:{
			type:String
		},
		priceIndex:{
			type:Number
		},
		min:{
			type:String
		},
		max:{
			type:String
		},
		gather:{
			type:Boolean
		},
		pfId:{
			type:String
		},
		qdId:{
			type:String
		},
		min_ml:{
			type:String
		},
		max_ml:{
			type:String
		}
		
	},
	created() {
		_this = this
		this.getGoodsList()
	
	},
	watch:{
		num(num){
			_this.getGoodsList()
		},
		carId(id){
			_this.getGoodsList()
		},
		brandId(){
			_this.getGoodsList()
		},
		priceIndex(id){
			_this.getGoodsList();
			id = "";
		},
		gather(bool){
			if(bool){
				_this.getGoodsList();
				this.$emit("handleGather",false)
			}
		}
	},
	filters:{
		filterImg(src){
			if(src){
				return config.domain + src
			}
		}
	},
	methods:{
		clickDetails(goodsId){
			goWindow('/other_pages/tebieDetails/tebieDetails?id=',goodsId)
		},
		// 获取商品列表
		getGoodsList(page_index = 1) {
			let _this = this;
			this.$store.commit("changeGroup",2) 
			request("/index/getUsedCarGoodsList", {
					page_index: page_index,
					page_size: 10,
					group_id:2,
					ai_sort:this.num,
					car_type:this.carId,
					brands_id:this.brandId,
					min_price:this.min,
					max_price:this.max,
					pf_id:this.pfId,
					qd_id:this.qdId,
					min_ml:this.min_ml,
					max_ml:this.max_ml,
				}, "GET")
				.then((res) => {
					this.$c.showLoading("加载中")
					let data = res.data.data.data
					if(page_index > 1){
						data = this.goodsList.concat(data)
					}
					this.goodsList = data;
					this.page_count = res.data.data.page_count;
					this.$emit("changePage",page_index,this.page_count)
					setTimeout(() => {
						uni.hideLoading();
					}, 300);
				})
		},
		handleErr(e, index) {
			if (e.type == 'error') {
				this.goodsList[index].pic_cover = "../../../static/images/list.jpg"
			}
		}
	}
}
